import React from 'react';
// import { robots } from "./robots"; // import const {robots} from robots.js file. robots does not export as 'default' thus needs to be deconstructed


const Card = (props) => {
    return(
        <div className='bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5 tc'>
            <img alt='robots' src={`https://robohash.org/${props.id}200x200`}/>
            <div>
                <h2>{props.name}</h2>
                <p>{props.email}</p>
            </div>
        </div>
    )
}


export default Card;